.card {
  width: 352px;
  height: 314px;
  border-radius: 6px;
  box-shadow: 0px 3px 6px #00000029;
  box-sizing: border-box;
  margin-bottom: 15px;
}
.card img {
  width: 352px;
  border-radius: 6px 6px 0px 0px;
}
.descripcion {
  height: 134px;
  position: relative;
  padding: 35px 0px 0px 30px;
  box-sizing: border-box;
  background: #ffffff;
}
.descripcion div {
  width: 83px;
  height: 32px;
  background-color: #fa6980;
  color: #fff;
  position: absolute;
  top: -20px;
  left: 30px;
  font-weight: 600;
  text-align: center;
  font-size: 16px;
  border-radius: 6px;
  line-height: 30px;
}
.descripcion h5 {
  font-size: 16px;
  margin: 0;
}
.descripcion p {
  font-size: 13px;
  color: #7a838d;
}
span {
  font-weight: 600;
}

@media screen and (max-width: 1340px) and (min-width: 835px) {
  .card {
    display: none;
  }
  .card:nth-child(1) {
    display: block;
  }
  .card:nth-child(2) {
    display: block;
  }
}
@media screen and (max-width: 835px) {
  .card {
    display: none;
  }
  .card:nth-child(1) {
    display: block;
  }
}
@media screen and (max-width: 500px) {
  .card {
    width: 286px;
    height: 180px;
    box-sizing: border-box;
  }
  .card img {
    width: 286px;
    height: 180px;
  }
}
